import React from "react";

import Container from "../atoms/Container";
import Text from "../atoms/Text";

import { useProfile } from "../../hooks/useProfile";

const Upgrade = (props) => {
  const { profile } = useProfile();

  return (
    <>
      {(profile.isDocumentUseTriggered && !profile.isDocumentVerified && !profile.isDocumentInReview) && (
        <Container p="12px" wide {...props}>
          <Container bg="orangered" p="6px" radius="8px" wide>
            <Text>
              Please upload ID documents,{" "}
              <Text
                p="0"
                underline="true"
                bold="true"
                to="/confirmation/documents/start"
              >
                {" "}
                Click here
              </Text>
            </Text>
          </Container>
        </Container>
      )}

      {profile.isDocumentInReview && (
        <Container p="12px" wide {...props}>
          <Container m="12px 0" bg="orangered" p="6px" radius="8px" shadow wide {...props}>
            <Text>
              Your ID document is currently being reviewed
            </Text>
          </Container>
        </Container>
      )}

      {profile.meta.requireUpgrade && (
        <Container p="12px" wide {...props}>
          <Container bg="orangered" p="6px" radius="8px" wide>
            <Text>
              Please Upgrade Your Account,{" "}
              <Text
                p="0"
                underline="true"
                bold="true"
                to="/dashboard/settings/verification"
              >
                {" "}
                Click here
              </Text>
            </Text>
          </Container>
        </Container>
      )}

      {profile.isDocumentRequested && (
        <Container p="12px" wide {...props}>
          <Container bg="orangered" p="6px" radius="8px" wide>
            <Text>
              Please Upload Required documents,{" "}
              <Text
                p="0"
                bold="true"
                underline="true"
                to="/confirmation/documents/upload"
              >
                {" "}
                Click here
              </Text>
            </Text>
          </Container>
        </Container>
      )}
    </>
  )
};

export default Upgrade;
